import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { products } from "./productData";
import Modal from './Modal';

const ProductAsCategory = () => {

    const { slug } = useParams()
    const { category } = useSelector((state) => state.category)
    const [selectedProduct, setSelectedProduct] = useState(null);

    const onItemClick = (product) => {
        setSelectedProduct(product);
    };

    const handleCloseModal = () => {
        setSelectedProduct(null);
    };

    return (
        <>
            <div className='container'>
                <div className="product_category_section">
                    <div className="pt-3">
                        <div className="productname_section" id={slug}>
                            <div className="container">
                                <p className="section-title py-2 reverse-title">{slug.replace("_", " ")}</p>
                            </div>
                        </div>
                        <div className="container section-spacer-mini">
                            <div className="row justify-content-center">
                                {products[slug.replace("_", " ")] && products[slug.replace("_", " ")].map((product) => (
                                    <div className="col-lg-4 col-md-6 my-2 p-0">
                                        <div className="category_section_card mx-2 h-100">
                                            <div
                                                className="card h-100"
                                                key={product.name}
                                                onClick={() => onItemClick(product)}
                                            >
                                                <div className="card-body d-flex align-items-center justify-content-center">
                                                    <img src={product.Image} alt={product.Image} className="pro-image" style={{ objectFit: "contain" }} />
                                                </div>
                                                <div className="card-footer">
                                                    <p className="small-tiny-description text-darkess-blue">
                                                        {product.name}
                                                    </p>
                                                    <p className="small-tiny-description">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {selectedProduct && (
                <Modal product={selectedProduct} onClose={handleCloseModal} />
            )}
        </>
    )
}

export default ProductAsCategory